<template>
  <transition name="modal">
    <div class="modal-mask">
      <div class="modal-wrapper">
        <div class="modal-container">
          <div class="default-layout layout_" v-if="modalLayout == 'default'">
            <div class="header">
              <button class="left-item invisible">
                <i class="fa fa-arrow-left"></i>
              </button>
              <h4 class="title-item">Join Modern Account</h4>
              <button class="right-item" @click="$emit('close')">
                <img src="@/assets/icons/close-icon.svg" alt="Close Icon" style="width: 25px;" />
              </button>
            </div>
            <div class="body">
              <button class="btn" @click="modalLayout = 'signupform'">Sign up with email</button>
              <button class="btn" @click="modalLayout = 'loginform'">Sign in</button>
              <div class="separator">OR</div>
              <button class="text-btn" @click="$emit('demoModalContinue')">Continue as Guest</button>
            </div>
          </div>

          <form
            class="login-form layout_"
            v-if="modalLayout == 'loginform'"
            action="#"
            method="post"
          >
            <div class="header">
              <button class="left-item" @click="modalLayout = 'default'">
                <img
                  src="@/assets/icons/arrow-left-icon.svg"
                  alt="Left Arrow Icon"
                  style="width: 40px;"
                />
              </button>
              <h4 class="title-item">Sign In</h4>
              <button class="right-item" @click="$emit('close')">
                <img src="@/assets/icons/close-icon.svg" alt="Close Icon" style="width: 25px;" />
              </button>
            </div>
            <div class="body">
              <div class="block">
                <div class="form-row">
                  <div class="form-group">
                    <label for="login_phone" class="sr-only">Phone Number</label>
                    <input type="tel" id="login_phone" placeholder="Phone Number" required />
                  </div>
                </div>
                <div class="form-group">
                  <label for="login_password" class="sr-only">Password</label>
                  <input type="password" id="login_password" placeholder="Password" required />
                </div>
                <div class="form-group">
                  <button
                    type="button"
                    class="m-btn m-btn-submit btn-full"
                    @click="$emit('demoModalContinue')"
                  >SIGN IN</button>
                </div>
                <div class="links">
                  <a href="#" @click.prevent="modalLayout = 'resetPasswordform'">Forgot Password</a>
                  <a href="#" @click.prevent="modalLayout = 'signupform'">Sign Up</a>
                </div>
              </div>
            </div>
          </form>

          <form
            class="signup-form layout_"
            v-if="modalLayout == 'signupform'"
            action="#"
            method="post"
          >
            <div class="header">
              <button class="left-item" @click="modalLayout = 'default'">
                <img
                  src="@/assets/icons/arrow-left-icon.svg"
                  alt="Left Arrow Icon"
                  style="width: 40px;"
                />
              </button>
              <h4 class="title-item">Sign Up</h4>
              <button class="right-item" @click="$emit('close')">
                <img src="@/assets/icons/close-icon.svg" alt="Close Icon" style="width: 25px;" />
              </button>
            </div>
            <div class="body">
              <div class="inline">
                <div class="form-row">
                  <div class="form-group">
                    <label for="signup_name" class="sr-only">Name</label>
                    <input type="text" id="signup_name" placeholder="Name" required />
                  </div>
                  <div class="form-group">
                    <label for="signup_password" class="sr-only">Email</label>
                    <input type="email" id="signup_password" placeholder="Email" required />
                  </div>
                </div>
                <div class="form-row">
                  <div class="form-group">
                    <label for="signup_dob" class="sr-only">Gender</label>
                    <select class="form-control">
                      <option value>Select Gender</option>
                      <option value="Male">Male</option>
                      <option value="Female">Female</option>
                    </select>
                  </div>
                  <div class="form-group">
                    <label for="signup_dob" class="sr-only">Date of Birth</label>
                    <input type="date" id="signup_dob" placeholder="Date of Birth" required />
                  </div>
                </div>
                <div class="form-row">
                  <div class="form-group">
                    <label for="signup_phone" class="sr-only">Phone Number</label>
                    <input type="tel" id="signup_phone" placeholder="Phone Number" required />
                  </div>
                  <div class="form-group">
                    <label for="signup_password" class="sr-only">Password</label>
                    <input type="password" id="signup_password" placeholder="Password" required />
                  </div>
                </div>
                <div class="form-group btn-wrapper-inline">
                  <button
                    type="button"
                    class="m-btn m-btn-submit inline-btn"
                    @click="$emit('demoModalContinue')"
                  >SIGN UP</button>
                </div>
                <div class="links center">
                  <span>Already have an account</span>
                  <a href="#" @click.prevent="modalLayout = 'loginform'">Sign In</a>
                </div>
              </div>
            </div>
          </form>

          <form
            class="resetpassword-form layout_"
            v-if="modalLayout == 'resetPasswordform'"
            action="#"
            method="post"
          >
            <div class="header">
              <button class="left-item" @click="modalLayout = 'default'">
                <img
                  src="@/assets/icons/arrow-left-icon.svg"
                  alt="Left Arrow Icon"
                  style="width: 40px;"
                />
              </button>
              <h4 class="title-item">Forgot Password</h4>
              <button class="right-item" @click="$emit('close')">
                <img src="@/assets/icons/close-icon.svg" alt="Close Icon" style="width: 25px;" />
              </button>
            </div>
            <div class="body">
              <div class="block">
                <div class="form-row">
                  <div class="form-group">
                    <label for="login_phone" class="sr-only">Phone Number</label>
                    <input type="tel" id="login_phone" placeholder="Phone Number" required />
                  </div>
                </div>
                <div class="form-group">
                  <button
                    type="button"
                    class="m-btn m-btn-submit btn-full"
                    @click="$emit('demoModalContinue')"
                  >SEND CODE</button>
                </div>
                <div class="links">
                  <a href="#" @click.prevent="modalLayout = 'loginform'">Sign In Here</a>
                </div>
              </div>
            </div>
          </form>

          <!-- <button class="modal-default-button" @click="$emit('close')">OK</button> -->
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: "AppModal",
  data() {
    return {
      modalLayout: "default",
      travelDate: null
    };
  },
  methods: {
    loginRequest: function(event) {
      event.preventDefault();
    }
  },
  mounted() {
    console.log("Loader");
  }
};
</script>

<style lang="scss" scoped>
.invisible {
  opacity: 0;
  visibility: hidden;
}
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  transition: opacity 0.3s ease;
}

.modal-wrapper {
  width: 100%;
  display: flex;
  align-items: center;
}

.modal-container {
  width: 800px;
  max-width: 100%;
  min-height: 300px;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
  transition: all 0.3s ease;
  font-family: Helvetica, Arial, sans-serif;
  @media (max-width: 768px) {
    margin: 1rem;
  }
  .layout_ {
    display: flex;
    flex-direction: column;
    min-height: 270px;
  }
  .body {
    flex: 1;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    padding-top: 2rem;
    padding-bottom: 2rem;
    .btn {
      padding: 10px 17px;
      min-height: 55px;
      min-width: 234px;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      border-radius: 10px;
      border: 1px solid #2f2d98;
      color: #2f2d98;
      outline: none;
      background-color: transparent;
      margin-bottom: 1rem;
      cursor: pointer;
    }
    .text-btn {
      -webkit-appearance: none;
      background: no-repeat;
      outline: none;
      border: none;
      color: #2f2d98;
      font-size: 1.12rem;
      border-bottom: 1px solid #2f2d98;
      padding-bottom: 0.4rem;
      cursor: pointer;
    }
    .separator {
      margin-bottom: 1rem;
    }
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0;
  .left-item,
  .right-item {
    cursor: pointer;
    -webkit-appearance: none;
    border: none;
    outline: none;
    background: no-repeat;
  }
  .title-item {
    color: var(--color-modern-blue);
    font-size: 1.4em;
    font-weight: 700;
    margin: 0;
  }
}

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.form-group {
  margin-bottom: 1.3rem;
  input {
    padding: 6px 14px;
    min-height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    -webkit-appearance: none;
    border: none;
    font-weight: 600;
    outline: none;
    color: #2f2d98;
    border: 1px solid #2f2d98;
    &::placeholder {
      color: #2f2d98;
      opacity: 0.7;
    }
  }

  .form-control {
    padding: 6px 14px;
    min-height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    border: none;
    font-weight: 600;
    outline: none;
    color: #2f2d98;
    border: 1px solid #2f2d98;
  }
}

.block {
  width: 400px;
  max-width: 100%;
}

.inline {
  width: 650px;
  max-width: 100%;
  .form-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .form-group {
      width: 48%;
    }
  }
}

.btn-wrapper-inline {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  text-align: center;
}

.inline-btn {
  display: inline-block;
  width: 300px;
  margin: 0 auto;
  max-width: 100%;
}

.btn-full {
  width: 100%;
}

.links {
  padding: 0.2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  span {
    display: inline-block;
    margin-right: 0.5rem;
  }
  &.center {
    justify-content: center;
  }
  a {
    color: #89b1ef;
    display: inline-block;
  }
}
</style>
