<template>
  <div class="booking-form-wrapper">
    <div class="form-container">
      <form method="post" v-on:submit="getSubRequest" action="#">
        <div class="fomr-header">
          <h5>Get The Modern Experience</h5>
          <h2>Book a ticket now</h2>
        </div>

        <div class="form-body">
          <div class="form-group">
            <label class="sr-only" id>Leaving From</label>
            <v-select
              v-model="formData.from"
              class="towns-selector"
              placeholder="Leaving From"
              label="town_name"
              :options="towns"
            ></v-select>
          </div>

          <div class="form-group">
            <label class="sr-only" id>Travelling To</label>
            <v-select
              v-model="formData.to"
              class="towns-selector"
              placeholder="Travelling To"
              label="town_name"
              :options="towns"
            ></v-select>
          </div>

          <div class="form-group">
            <label class="sr-only">Travelling On</label>
            <DatePicker
              v-model="travelDate"
              :min-date="new Date()"
              :max-date="maxTravelDate"
              :input-props="{
                class: 'vc-calendar-custom',
                placeholder: 'Travelling On',
                readonly: true
              }"
            />
          </div>

          <!-- Submit Form -->
          <div class="form-group">
            <button class="m-btn m-btn-submit">SEARCH AVAILABLE BUSES</button>
          </div>
          <!-- /Submit Form -->
        </div>
      </form>
    </div>
  </div>
</template>

<script>
/* Towns data, TODO: Fetch from API */
let towns = [
  { id: 1, town_name: "Nairobi" },
  { id: 2, town_name: "Mombasa" }
];

/* Max date, 4 days from today */
let getMaxTravelDate = () => {
  let today = new Date();
  let tomorrow = new Date(today);
  return tomorrow.setDate(tomorrow.getDate() + 4);
};

export default {
  name: "BookingForm",
  data() {
    return {
      travelDate: null,
      maxTravelDate: getMaxTravelDate(),
      towns: towns,
      formData: {
        to: "",
        from: ""
      }
    };
  },
  methods: {
    getSubRequest: function(event) {
      event.preventDefault();
      this.$router.push({ name: "Buses", params: { userId: "123" } });
    }
  },
  mounted() {
    console.log("Loader");
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.booking-form-wrapper {
  display: flex;
  padding: 2.5rem 1rem 1rem 1rem;
  position: relative;
  z-index: 1;
  width: 40%;
  @media (max-width: 768px) {
    width: 100%;
    background-color: var(--color-modern-blue);
    border-top-left-radius: 60px;
    border-top-right-radius: 60px;
  }
}
.form-container {
  margin-left: 1rem;
  width: 80%;
  @media (max-width: 768px) {
    width: 100%;
    margin-left: 0;
    padding: 1rem;
  }
}
.fomr-header {
  h5 {
    margin: 0;
    font-weight: 600;
    color: #fff;
  }
  h2 {
    font-size: 1.8rem;
    color: #fff;
    font-weight: 700;
    margin-top: 0.8rem;
    @media (max-width: 768px) {
      font-size: 1.5rem;
    }
  }
}
.form-group {
  padding: 1rem 0;
}
</style>
