<script>
//AppHeader component
import AppHeader from "@/components/AppHeader.vue";

//AppFooter component
import AppFooter from "@/components/AppFooter.vue";

//AppModal component
import AppModal from "@/components/AppModal.vue";

//Passanger Model
import AppPassangerForm from "@/components/AppPassangerForm.vue";

/* Towns data, TODO: Fetch from API */
let booking_point = [{ id: 1, town_name: "Accra Road" }];

let dropping_point = [{ id: 1, town_name: "Total Moi Avenue" }];

/* Max date, 4 days from today */
let getMaxTravelDate = () => {
  let today = new Date();
  let tomorrow = new Date(today);
  return tomorrow.setDate(tomorrow.getDate() + 4);
};

export default {
  name: "Buses",
  components: {
    AppHeader,
    AppFooter,
    AppModal,
    AppPassangerForm
  },
  data() {
    return {
      showModal: false,
      showBusSeatLayout: false,
      showPassangerForm: false,
      showTripPreviewLayout: false,
      travelDate: null,
      maxTravelDate: getMaxTravelDate(),
      bookingPoints: booking_point,
      droppingPoints: dropping_point,
      formData: {
        to: "",
        from: ""
      },
      activeTableCols: 5,
      activeTableRows: 3,
      buseData: {},
      seats: null
    };
  },
  methods: {
    passangerFormSubmitted: function() {
      this.showTripPreviewLayout = true;
    },
    showPassangerComponent: function() {
      this.showModal = false;
      this.showPassangerForm = true;
    },
    bookingContinue: function() {
      //TODO Check if user is loggedIn
      this.showModal = true;
    },
    filterRequest: function(event) {
      event.preventDefault();
    },
    seatSelected: function(seat_) {
      console.log(seat_);
    },
    getSelectedBusSeats: function() {
      console.log("hello");
      this.showBusSeatLayout = true;
      this.getBusDetailsRequest();
    },
    getBusDetailsRequest: function() {
      let self = this;
      var requestOptions = {
        method: "GET",
        redirect: "follow"
      };

      fetch("http://localhost/modern-api/getBusInfo.php", requestOptions)
        .then(response => {
          return response.json();
        })
        .then(data => {
          let _busData = data.bus_type;
          self.seats = data.seats;
          self.buseData = data.bus_type;
          self.activeTableCols = _busData.bus_type_cols;
          self.activeTableRows = _busData.bus_type_rows;
        })
        .catch(error => console.log("error", error));
    }
  }
};
</script>

